
.Home {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Home img {
    width: 60px;
    margin-left: 50px;
    margin-top: 20px;
}

.Home ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-right: 50px;
}

.Home li {
    list-style-type: none;
    margin-left: 60px;
}

.Home a {
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    font-weight: 600;
}

.Home a:hover {
    text-decoration: underline;
    color: #0BBFBF;
    transition: all 0.5s ease;
}