
.introducao {
    color: #fff;
}

.introducao span {
    font-size: 18px;
}

.gradient-text {
    background: linear-gradient(to left, #6D00FF, #C10CE8 , #150CE8 );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
}

.gradient-text h1 {
    font-size: 40px;
}